<template>
  <div class="page-button">
    <calf-button></calf-button>
    <calf-button :disabled="true"></calf-button>
    <calf-button :invalid="true"></calf-button>
    <calf-button :inline="true"></calf-button>
    <calf-button :minor="true"></calf-button>
    <calf-button :minor="true" :disabled="true"></calf-button>
    <calf-button :minor="true" :invalid="true"></calf-button>
    <calf-button>
      <span slot="content"></span>
      <span slot="desc">01074047</span>
    </calf-button>
    <calf-button :disabled="true">
      <span slot="content"></span>
      <span slot="desc">01074047</span>
    </calf-button>
    <calf-button :invalid="true">
      <span slot="content"></span>
      <span slot="desc">01074047</span>
    </calf-button>
    <calf-button :icon="icon" @click="handleLoading">loading</calf-button>
    <calf-button @click="handleClick"></calf-button>
    <calf-button @click="handleBack"></calf-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tid: '',
      icon: ''
    }
  },
  methods: {
    handleClick() {
      this.$createDialog({
        content: '',
        onConfirm: () => {
          console.log('confirm')
        }
      }).show()
    },
    handleLoading() {
      this.icon = 'loading'
      clearTimeout(this.tid)
      this.tid = setTimeout(() => {
        this.icon = 'success'
      }, 3000)
    },
    handleBack() {
      history.go(-1)
    }
  }
}
</script>

<style lang="postcss" scoped>
.page-button {
  padding: 20px;
  .calf-button {
    margin: 10px 0;
  }
}
</style>
