<template>
 <div class="page-toast">
   <calf-button @click="handleClick1">toast</calf-button>
   <calf-button @click="handleClick2">toast</calf-button>
   <calf-button @click="handleClick3">toast3s</calf-button>
   <calf-button @click="handleClick4">toast</calf-button>
 </div>
</template>

<script>
export default {
  name: 'page-toast',
  data() {
    return {}
  },
  props: {},
  computed: {},
  watch: {},
  methods: {
    handleClick1() {
      this.$createToast({
        content: '100'
      }).show()
    },
    handleClick2() {
      this.$createToast({
        content: ''
      }).show()
    },
    handleClick3() {
      this.$createToast({
        duration: 3 * 1000,
        content: '',
        onClose: () => {
          console.log('close')
        }
      }).show()
    },
    handleClick4() {
      this.$createToast({
        duration: 3 * 1000,
        // icon: 'tick-red',
        icon: 'close',
        content: '...',
        onClose: () => {
          console.log('close')
        }
      }).show()
    }
  },
  components: {},
  mounted() {}
}
</script>

<style lang="postcss" scoped>
.page-toast {
  padding: 20px;
}

.calf-button {
  margin: 10px 0;
}
</style>
