<template>
  <div class="page-pull-refresh">
    <h1 class="title">title</h1>
    <div class="content">
      <calf-pull-refresh
        ref="index"
        :distanceIndex="2"
        :wrapperHeight="wrapperHeight"
        :top-load-method="handRefresh"
        :bottom-load-method="handLoadMore"
        @top-state-change="handTopStateChange"
        @top-pull="handTopPull"
        @bottom-state-change="handBottomStateChange"
        @bottom-pull="handBottomPull"
        @infinite-scroll="handNativeScroll"
        >
        <!--
          <div slot="top-block" class="top-block"></div>
         -->
        <ul class="list">
          <li class="item" v-for="(item, index) in num" :ref="index" :key="index">{{item}}</li>
        </ul>
        <!--
          <div slot="bottom-block" class="bottom-block"></div>
         -->
      </calf-pull-refresh>
    </div>
  </div>
</template>

<script>
export default {
  name: 'page-pull-refresh',
  data() {
    return {
      wrapperHeight: '0px',
      num: 15
    }
  },
  props: {},
  computed: {},
  watch: {},
  methods: {
    handRefresh(loaded) {
      // loaded 
      // ajaxloadedtop-block'done'
      setTimeout(() => {
        loaded('done')
      }, 10 * 1000)
    },
    handTopPull(dis) {
      // dis  px  >0 
      console.log(dis)
    },
    handTopStateChange(state) {
      // state  pull trigger loading loaded-done 
      // top-block
      console.log(state)
    },
    handLoadMore(loaded) {
      // 
      setTimeout(() => {
        this.num += 5
        loaded('done')
      }, 1000)
    },
    handBottomStateChange(state) {
      // 
      console.log(state)
    },
    handBottomPull(dis) {
      // dis  px  <0 
      console.log(dis)
    },
    handNativeScroll() {
      // 
    }
  },
  mounted() {
    // wrapperHeight   100%  px
    // 
    this.wrapperHeight =
      document.querySelector('.page-pull-refresh').clientHeight -
      document.querySelector('.title').clientHeight +
      'px'
  }
}
</script>

<style lang="postcss" scoped>
.page-pull-refresh {
  height: 100%;
  .title {
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background: #f6f6f6;
    z-index: 999;
  }
  .content {
    position: relative;
    z-index: 1;
    .list {
      .item {
        box-sizing: border-box;
        width: 100%;
        height: 50px;
        line-height: 50px;
        padding-left: 16px;
        &:nth-child(2n + 1) {
          background: #f6f6f6;
        }
      }
    }
  }
}
</style>
